@import "../../assets/styles/app";

.buyTokens {
  @include container;
  padding-top: 48px;
  padding-bottom: 24px;


  @include desktop {
    padding-top: 128px;
    padding-bottom: 64px;
  }

  .inner {
    @include inner;

    h2.title {

      p {
        @include Hind;
        font-size: 40px;
        line-height: 85%;
        letter-spacing: -0.02em;
        text-align: center;
        color: #fff;

        @include desktop {
          font-size: 100px;
        }

        &:nth-child(2) {
          margin-top: 8px;

          @include desktop {
            margin-top: 0;
          }
        }

        svg {
          position: relative;
          width: 48px;
          height: 48px;
          top: 12px;

          @include desktop {
            width: 64px;
            height: 64px;
            top: 4px;
          }
        }
      }
    }

    .startBtn {
      margin-top: 24px;
      width: 212px;
      height: 60px;
      margin-left: auto;
      margin-right: auto;

      @include desktop {
        margin-top: 32px;
      }
    }

    .wrapper {
      margin-top: 48px;
      display: flex;

      @include desktop {
        margin-top: 96px;
        flex-direction: column;
      }

      .progress {
        flex-shrink: 0;
        position: relative;
        border-radius: 999px;
        width: 28px;
        backdrop-filter: blur(4px);
        background: rgba(255, 255, 255, 0.1);

        @include desktop {
          height: 28px;
          width: 100%;
        }

        .progressInner {
          position: relative;
          width: 100%;
          height: 0%;
          background: #FFF;
          border-radius: 999px;

          @include desktop {
            width: 0%;
            height: 100%;
          }

          &::before {
            content: "";
            display: block;
            position: absolute;
            left: 50%;
            top: 4px;
            transform: translateX(-50%);
            width: 24px;
            height: 24px;
            border-radius: 50%;
            background: #010302;

            @include desktop {
              left: 4px;
              top: 50%;
              transform: translateY(-50%);
            }
          }

          &::after {
            content: "";
            display: block;
            position: absolute;
            left: 50%;
            bottom: 4px;
            transform: translateX(-50%);
            width: 24px;
            height: 24px;
            border-radius: 50%;
            background: #010302;

            @include desktop {
              left: auto;
              right: 4px;
              bottom: auto;
              top: 50%;
              transform: translateY(-50%);
            }
          }
        }

      }

      .items {
        margin-left: 20px;

        @include desktop {
          margin-left: 0;
          margin-top: 32px;
          display: grid;
          grid-template-columns: repeat(3, 1fr);
          grid-column-gap: 106px;
        }

        .item {
          position: relative;
          background: #010302;

          @include desktop {
            border: 1px solid rgba(255, 255, 255, 0.1);
            border-radius: 20px;
            padding: 20px;
          }

          &:not(:first-child) {
            margin-top: 32px;

            @include desktop {
              margin-top: 0;
            }
          }

          p:nth-child(1) {
            font-weight: 500;
            font-size: 20px;
            line-height: 125%;
            color: #fff;
          }

          p:nth-child(2) {
            margin-top: 8px;
            @include Hind;
            font-size: 18px;
            line-height: 140%;
            color: rgba(255, 255, 255, 0.4);

            @include desktop {
              margin-top: 12px;
            }
          }
        }
      }

    }

  }
}